div,h1,h2,h3,h4,h5,h6,p,li,a,span,strong {
	overflow-wrap: normal;
    word-wrap: normal;
    -ms-word-break: normal;
    word-break: normal;
    word-break: normal;
    -ms-hyphens: none;
    -moz-hyphens: none;
    -webkit-hyphens: none;
    hyphens: none;
    page-break-inside: avoid;
}
* {
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}
pre {
	font-family: $font_pre;
	font-size: 13px;
	line-height: 1.3;
}
body {
	font-family: $font_primary;
	font-size: 14px;
	line-height: 1.3;
	color: #000;
}
section, div {
	&:focus {
		outline: 0;
	}
}
a,input,button,select,textarea {
	transition: all ease .3s;
}
a {
	color: $red;
	&:hover {
		color: tint($red,50%);
	}
}
.cf:before, .cf:after {
    content: " "; 
    display: table; 
}
.cf:after { clear: both; }
.cf { *zoom: 1; }
.text-center {text-align:center;}
.full {width:100%!important;float:left !important}

/* Text meant only for screen readers. */
.sr {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
	&:focus {
		background-color: #333;
		border-radius: 3px;
		box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
		clip: auto !important;
		color: #FFF;
		display: block;
		@include font-size(0.875);
		font-weight: bold;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 15px 23px 14px;
		text-decoration: none;
		top: 5px;
		width: auto;
		z-index: 100000; /* Above WP toolbar. */
	}
}
.wrapper {
	max-width: 1200px;
	width: 100%;
	margin: 0 auto;
	padding-left: 20px;
	padding-right: 20px;
	position: relative;
}
.site {
	position: relative;
	overflow-x: hidden;
	overflow-y: auto;
}
.flexwrap {
	@include flexgroup();
}
/* Header */
.site-header {
	position: fixed;
	top: 30px;
	left: 0;
	width: 100%;
	z-index: 200;
}
.topbar {
	width: 100%;
	height: 30px;
	float: left;
	background: #000;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 99995;
}
.topinfo {
	display: block;
	width: 100%;
	color: #FFF;
	position: relative;
	@include flexgroup();
	@include flexbox-justify(center);
	@include tab {
		display: inline-block;
		float: right;
		width: auto;
	}
	a {
		display: inline-block;
		text-decoration: none;
		color: #FFF;
		cursor: pointer;
	}
	span.el {
		display: inline-block;
		position: relative;
		line-height: 30px;
		font-weight: 200;
		font-size: 13px;
		position: relative;
		padding-right: 15px;
		padding-left: 13px;
		@include mid {
			font-size: 12px;
		}
		&:after {
			content: "";
			display: block;
			width: 1px;
			height: 11px;
			background: rgba(255,255,255,.45);
			position: absolute;
			top: 9px;
			right: 0;
		}
		.search {
			padding-left: 22px;
			&.search-open {
				opacity: 0.2;
			}
		}
		.spanlink {
			&:hover {
				opacity: 0.5;
			}
		}
	}
	span.el:first-child {
		&:before {
			display: none;
		}
	}
	span.daily-schedule {
		display: none;
		@include tab {
			display: inline-block;
		}
		a {
			display: inline-block;
			position: relative;
			em {
				font-style: normal;
			}
			em.ds-txt {
				padding-right: 23px;
			}
			em.ds-stat {
				display: block;
				width: 10px;
				height: 10px;
				background: #ef0808;
				border-radius: 100px;
				position: absolute;
				top: 11px;
				right: 0;
			}
		}
		&.open {
			a em.ds-stat {
				background: #3AFF05;
			}
		}
	}
	span.srchLink {
		padding-left: 0;
		margin-left: 0;
		&:after {
			display: none;
		}
	}
	span.toplink,
	span.trail-status {
		padding-right: 38px;
		margin-left: 0;
		span.s {
			display: block;
			width: 10px;
			height: 10px;
			background: #ef0808;
			border-radius: 100px;
			position: absolute;
			top: 11px;
			right: 15px;
		}
		&.active {
			span.s {
				background: #3AFF05;
			}
		}
	}
	span.today {
		padding-left: 0;
		padding-right: 10px;
		margin-left: 0;
		a {
			display: block;
			padding: 0 32px 0 12px;
			position: relative;
			cursor: pointer;
			z-index: 60;
		}
		i {
			font-style: normal;
		}
		i.arrow {
			display: block;
			width: 8px;
			height: 8px;
			position: absolute;
			top: 10px;
			right: 8px;
			transition: all ease .3s;
			&:before, &:after {
				content:"";
				display: block;
				width: 100%;
				height: 1px;
				background: #FFF;
				position: absolute;
				top: 5px;
			} 
			&:before {
				transform: rotate(45deg);
				left: -3px;
			}
			&:after {
				transform: rotate(-45deg);
				right: -2px;
			}
		}
		// &:hover {
		// 	a i.arrow {
		// 		transform: translateY(3px) rotate(180deg);
		// 	}
		// }
	}
	.businessHours {
		position: absolute;
		top: 0;
		right: -5px;
		width: 200px;
		z-index: 20;
		padding-top: 30px;
		transition: all ease .3s;
		height: 0;
		max-height: 0;
		overflow: hidden;
		opacity: 0;
		transform: translateY(-30px);
		* {
			transition: all ease .3s;
		}
		ul {
			margin: 0 0;
			padding: 5px 15px;
			list-style: none;
			background: rgba(0,0,0,.75);
			li {
				position: relative;
				margin: 15px 0;
				padding-left: 32px;
				font-family: $font_secondary;
				font-size: 12px;
				line-height: 1.3;
			}
		}
		.n {
			text-transform: uppercase;
		}
		.d {
			font-style: italic;
			font-size: 9px;
			margin-top: 5px;
		}
		.icon {
			position: absolute;
			top: 2px;
			left: -2px;
			i {
				font-size: 20px;
			}
			svg {
				font-size: 20px;
				line-height: 1;
			}
		}
		
		.tdlink {
			display: block;
			text-decoration: none;
			padding: 0 0;
			cursor: pointer;
			* {
				cursor: pointer;
			}
			&:hover {
				.t1, .t2 {
					opacity:0.5;
				}
			}
		}
	}
	span.today.open {
		.businessHours {
			height: auto;
			max-height: 100%;
			opacity: 1;
			overflow: visible;
			transform: translateY(0);
			z-index: 50;
		}
		a i.arrow {
			transform: translateY(3px) rotate(180deg);
		}
	}
}

