#overlay {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	background: rgba(0,0,0,.45);
	display: none;
	@include animateThis(0.5s);
}

#site-navigation {
	position: fixed;
	top: 30px;
	right: 0;
	max-width: 0;
	width: 0;
	height: 100%;
	z-index: 9999;
	transform: translateX(120%);
	transition: all ease .3s;
	backface-visibility: hidden;
	overflow: hidden;
	&.open {
		transform: translateX(0);
		max-width: $nav_width;
		width: 100%;
		box-shadow: -1px 4px 30px rgba(0,0,0,0.3);
		overflow: visible;
	}
	.nav-inner {
		background: rgba($red,.95);
		padding: 90px 0 50px;
		height: 100%;
		overflow-x: hidden;
		overflow-y: auto;
		position: relative;
		z-index: 20;
	}
	.navigation {
		display: block;
		width: 100%;
		text-align: center;
	}
	ul {
		margin: 0 0;
		padding: 0 0;
		list-style: none;
	}
	ul.menu {
		width: 100%;
		text-align: right;
		margin-top: 0;
		margin-bottom: 30px;
	}
	ul.menu > li {
		position: relative;
		transition: all ease .3s;
		&:after {
			content:"";
			display: block;
			width: 0%;
			height: 100%;
			background: shade($red,15%);
			position: absolute;
			top: 0;
			right: 0;
			opacity: 0;
			transition: all ease .5s;
		}
	}
	ul.menu > li > a {
		display: block;
		color: #FFF;
		font-family: $font_secondary;
		font-size: 18px;
		font-weight: 400;
		line-height: 1.2;
		letter-spacing: 2px;
		text-decoration: none;
		text-transform: uppercase;
		padding: 14px 60px 14px 50px;
		position: relative;
		z-index: 10;
		span {
			display: inline-block;
			position: relative;
			&:before,&:after {
				content:"";
				display: block;
				background: #FFF;
				position: absolute;
				top: 10px;
				right: -15px;
			}
			&:before {
				width: 9px;
				height: 1px;
			}
			&:after {
				height: 9px;
				width: 1px;
				margin-top: -4px;
				margin-right: 4px;
			}
		}
	}
	ul.menu > li > a:focus {

	}
	ul.menu > li.active,
	ul.menu > li:hover {
		&:after {
			opacity: 1;
			width: 100%;
		}
	}
}
.navigation-children {
	position: fixed;
	top: 0;
	right: $nav_width;
	width: 0;
	height: 100%;
	// background: rgba(255,255,255,.95);
	// box-shadow: -1px 4px 30px rgba(0,0,0,0.2);
	// transition: all ease .2s;
	backface-visibility: hidden;
	.navchild-inner {
		// padding: 90px 30px;
		width: 100%;
		height: 100%;
		position: relative;
    z-index: 99;
    overflow-x: hidden;
		overflow-y: auto;
	}
	.children-menu-wrap {
		column-count: 2;
		// column-gap: 1.5em;
		// margin: 1.5em 0;
	}
	.children-menu-content {
		margin-bottom: 10px;
		padding: 15px;
	}
	.submenu-name {
		font-family: $font_secondary;
		font-weight: 500;
		font-size: 15px;
		line-height: 1.2;
		text-transform: uppercase;
		margin-bottom: 10px;
	}
	.submenu {
		a {
			display: inline-block;
			text-decoration: none;
			color: $submenu_color;
			font-size: 13px;
    	padding: 6px 0;
    	&:hover {
    		color: $red;
    	}
		}
	}
	.submenu li.current_page_item > a {
		color: $red;
	}
	.children-group {
		position: absolute;
		top: 0;
		left: 0;
		background: rgba(255,255,255,.95);
		box-shadow: -1px 4px 30px rgba(0,0,0,0.2);
		padding: 75px 30px 80px;
		width: 100%;
		height: 100%;
		overflow-x: hidden;
		overflow-y: auto;
		display: none;
		@include animateThis(0.5s);
		&.open {
			display: block;
			// @include animateThis(0.5s);
			animation-name: fadeInRight;
		}
	}
	&.open {
		width: $nav_width + 100px;
	}
	.parent-name {
		display: none;
	}
}

.secondary-menu {
	padding: 35px 50px 20px;
	ul.menu2 {
		margin: 0 0;
		padding: 0 0;
		list-style: none;
	}
	li {
		text-align: right;
		a {
			font-family: $font_secondary;
			font-weight: 300;
			font-size: 14px;
			line-height: 1.2;
			text-transform: uppercase;
			letter-spacing: 1px;
			display: inline-block;
			text-decoration: none;
			color: #FFF;
			position: relative;
			padding: 8px 25px 8px 10px;
			.navIcon {
				position: absolute;
				top: 6px;
				right: 0;
				font-size: 18px;
				&.fa-calendar-alt {
					right: 3px;
				}
			}
			&:hover {
				opacity: 0.4;
			}
		}
	}
}

.closeNav {
	display: block;
	width: 25px;
	height: 25px;
	color: #FFF;
	text-align: center;
	border: 1px solid #FFF;
	border-radius: 100px;
	position: absolute;
	top: 30px;
	right: 30px;
	transition: all ease .3s;
	overflow: hidden;
	z-index: 100;
	&#closeNavChild {
		display: none;
	}
	span {
		display: block;
		width: 100%;
		height: 100%;
		text-indent: -999em;
		position: relative;
		&:before, &:after {
			content:"";
			display: block;
			width: 66%;
			height: 2px;
			background: #FFF;
			position: absolute;
			top: 10px;
			left: 4px;
			transition: all ease .3s;
		}
		&:before {
			transform: rotate(45deg);
		}
		&:after {
			transform: rotate(-45deg);
		}
	}
	&:hover {
		background: shade($red,20%);
		span {
			&:before, &:after {
				background: #FFF!important;
			}
		}
	}
}

.menu-toggle {
	display: block;
	width: 40px;
	height: 40px;
	float: right;
	background: transparent;
	-webkit-appearance: none;
	border: none;
	box-shadow: none;
	padding: 10px;
	position: relative;
	transition: all ease .3s;
	span.bar {
		width: 25px;
		top: 50%;
		transform: translateY(-50%);
		left: 8px;
	}
	span.bar,
	span.bar:before,
	span.bar:after {
		height: 4px;
		background: #000;
		position: absolute;
		border-radius: 2px;
	}
	span.bar:before, span.bar:after {
		content:"";
		display: block;
		width: 100%;
		top: 0;
		left: 0;
	}
	span.bar:before {
		top: -8px;
	}
	span.bar:after {
		top: 8px;
	}
}

body.nav-open {
	overflow: hidden;
	#overlay {
		display: block;
		animation-name: fadeIn;
	}
	.menu-toggle {
		opacity: 0;
	}
}

/* Slider */
#banner {
	width: 100%;
	float: left;
	background-color: #000;
	.static-banner,
	.flexslider {
		border-radius: 0px;
		.slides {
			.iframe-wrapper {
				padding: 0 0;
				position: relative;
			}
			img.image-size-ref {
				visibility: hidden;
			}
			.outer-video-wrap {
				width: 100%;
				height: 100%;
			}
			.videoIframeDiv {
				width: 100%;
				height: 100%;
				background-size: cover;
				background-position: center;
				background-repeat: no-repeat;
				position: absolute;
				top: 0;
				left: 0;
				&.playing {
					background-image:none!important;
					background-color: #000;
				}
				&:before {
					content:"";
					display: block;
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					background: rgba(0,0,0,.45);
				}
				iframe {
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					display: none;
					z-index: 50;
					&.iframe-vimeo {
						top: 2vh;
					}
				}
				.playButtonDiv {
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					text-align: center;
					z-index: 15;
					@include flexgroup-center();
					@include flexbox-justify(center);
				}
				.playVidBtn {
					display: inline-block;
					width:15vw;
					height: 15vw;
					position: relative;
					z-index: 30;
					background: rgba(255,255,255,.35);
					border: 2px solid #FFF;
					border-radius: 100%;
					@include desk {
						width: 130px;
						height: 130px;
					}
					&:before {
						content:"";
						width: 0;
						height: 0;
						border-left: 50px solid #FFF;
						border-top: 25px solid transparent;
						border-bottom: 25px solid transparent;
						position: absolute;
						top: 50%;
						left: 56%;
						z-index: 5;
						transform: translate(-50%,-50%);
					}
					span {
						display: inline-block;
						width: 0;
						width: 0;
						text-indent: -999em;
						overflow:none;
						background: rgba(255,255,255,.85);
					}
					&:hover {
						background: rgba(0,0,0,.45);
					}

				}

			}
		}
	}
	.slideCaption {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 10;
		@include flexgroup-center();
		.text {
			width: 100%;
			text-align: center;
			color: #FFF;
			font-size: 40px;
			font-family: $font_secondary;
			font-weight: 500;
			line-height: 1.2;
			text-transform: uppercase;
		}
	}
	.static-banner {
		width: 100%;
		float: left;
		ul.slides, li.slideItem, .image-wrapper {
			width: 100%;
			float: left;
			position: relative;
		}
		.image-wrapper {
			img {
				width: 100%;
				height: auto;
				float: left;
			}
			&.yes-mobile {
				img {
					&.desktop {
						display: none;
						@include tab {
							display: block;
						}
					}
					&.mobile {
						display: block;
						@include tab {
							display: none;
						}
					}
				}
			}
		}
		.slideCaption,
		.slidePageTitle {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 5;
			@include flexgroup-center();
			text-align: center;
			.innertext {
				width: 100%;
				text-align: center;
				margin-top: 12vh;
			}
			.text {
				display: inline-block;
				width: auto;
				padding: 0.5em 1em 0.4em;
				background: rgba(0, 0, 0, 0.85);
				color: #FFF;
				font-family: $font_secondary;
				font-size: 2em;
				font-weight: 300;
				text-transform: uppercase;
				letter-spacing: 2px;
				float: none;
				margin: 0 auto;
			}
		}
	}
	.slideTopTitle {
		width: 100%;
		color: #FFF;
		background: rgba($secondary,0.85);
		position: absolute;
		top: 88px;
		left: 0;
		z-index: 100;
		.wrapper {
			padding-top: 8px;
			padding-bottom: 8px;
		}
		.toptext {
			font-size: 18px;
			font-weight: 300;
			line-height: 1.2;
		}
	}
}

#subpageSlides {
	position: relative;
	.sub-slide-item {
		width: 100%;
		float: left;
		.slide-image {
			width: 100%;
			float: left;
			min-height: 200px;
			background-size: cover;
			background-position: center;
			img {
				width: 100%;
				height: auto;
				float: left;
			}
		}
	}
	.flex-direction-nav {
		display: none;
	}
	.flex-control-paging {
		margin: 0 0;
		padding: 0 0;
		list-style-type: none;
		width: 100%;
		position: absolute;
		bottom: 10px;
		left: 0;
		z-index: 50;
		li {
