	}
	.postbox {
		width: 100%;
		@include tab {
			width: 25%;
		}
		.inside {
			display: block;
			width: 100%;
			height: 100%;
			float: left;
			position: relative;
			text-decoration: none;
			color: #FFF;
		}
		.imagediv {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 10;
			background-size: cover;
			background-position: top center;
			background-repeat: no-repeat;
		}
		img.feat-img {
			width: 100%;
			height: auto;
			float: left;
			visibility: hidden;
		}
		&.no-image {
			.inside {
				background-color: #d8d8d8;
			}
		}
		.details {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 20;
			overflow:hidden;
			@include flexgroup-center();
			text-align: center;
			.info {
				width: 100%;
				padding: 5px;
				position: relative;
				z-index: 30;
				opacity: 0;
				transition: all ease .5s;
				transform: translateY(25%);
			}
			.event-name {
				font-family: $font_secondary;
				font-size: 18px;
				font-weight: 300;
				line-height: 1.2;
				letter-spacing: 1px;
				text-transform: uppercase;
			}
			* {
				transition: all ease .3s;
			}
		}

		div.wave {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			overflow:hidden;
			opacity: 0.7;
		}
		.wave-svg {
			background-image:url("images/wave.svg");
			background-repeat: no-repeat;
			background-position: top left;
			position: absolute;
			top: -30%;
			left: 0;
			width: 75vw;
			height: 100%;
			transition: all ease .8s;
			transform: translateY(150%);
			// animation: waveEffect 0.3s cubic-bezier(.55, .5, .45, .5) infinite;
			animation: waveEffect 0.4s cubic-bezier(.55, .55, .55, .55) infinite;
			&:before {
				content:"";
				display: block;
				width: 100%;
				height: 110%;
				background: #000;
				position: absolute;
				bottom: -30%;
				left: 0;
				transition: all ease 5s;
			}
		}
		.boxlink:hover {
			.details .info {
				opacity: 1;
				transform: translateY(0);
			}
			.wave-svg {
				transform: translateY(0);
				background-position: top right;
			}
		}
	}
	&.festival {
		.postbox {
			width: 100%;
			@include xs {
				width: 50%;
			}
			@include tab {
				width: 25%;
			}
		}
	}
	@media screen and (max-width:$tabwidth) {
		.postbox .wave-svg {
			width: 200vw;
		}
	}
}

