/* SEARCH BAR */
.top-search-bar {
	width: 100%;
	height: 0;
	background: #000;
	position: absolute;
	top: 30px;
	left: 0;
	transition: all ease .3s;
	z-index: 210;
	box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.35);
	overflow:hidden;
	// transform: translateY(-100%);
	&.show {
		opacity: 1;
		height: 100px;
		transform: translateY(0);
	}
	* {
		box-sizing: border-box;
	}
	.screen-reader-text {
		display: none;
	}
	.wrapper {
		height: 100%;
		@include flexgroup-center();
	}
	.form-wrapper {
		max-width: 80%;
		width: 100%;
		margin: 0 auto;
		position: relative;
	}
	form {
		width: 100%;
		position: relative;
		top: -5%;
		* {
			box-sizing: border-box;
		}
	}
	label {
		display: block;
		width: 100%;
		margin: 0 0;
	}
	input.search-field {
		width: 100%;
		-webkit-appearance: none;
		border-radius: 0px;
		border: none;
		background-color: transparent;
		box-shadow: none;
		border-bottom: 2px solid #565656;
		height: 50px;
		line-height: 45px;
		font-size: 25px;
		color: #FFF;
		outline: none;
		padding: 3px 15px;
		padding-left: 45px;
		font-weight: 300;
	}
	input.search-submit {
		width: 50px;
		height: 100%;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 10;
		visibility: hidden;
		opacity: 0;
	}
	#topsearchBtn {
		cursor: pointer;
		font-size: 22px;
		line-height: 1;
		position: absolute;
		top: 3px;
		left: -11px;
		z-index: 50;
		width: 50px;
		height: 100%;
		color: #c1c1c1;
		text-align: center;
		@include flexgroup-center();
		@include flexbox-justify(center);
		.fa-search {
			display: block;
			position: relative;
			top: -3px;
		}
	}
	#closeTopSearch {
		display: inline-block;
		cursor: pointer;
		width: 40px;
		height: 100%;
		position: absolute;
		top: 0;
		right: 0;
		background-color: transparent;
		span {
			display: none;
		}
		&:before, &:after {
			content: "";
			display: block;
			width: 50%;
			height: 3px;
			background: #c1c1c1;
			position: absolute;
			left: 13px;
			top: 25px;
		}
		&:before {
			transform: rotate(45deg);
		}
		&:after {
			transform: rotate(-45deg);
		}
	}
}

