.collapsible {
	padding: 30px 20px;
	border-top: 1px solid #bdbdbd;
	transition: all ease .3s;
	cursor: pointer;
	position: relative;
	@include animateThis(1s);
	&.first {
		border-top: none;
	}
	&:before {
		content:"";
		display: block;
		width: 100%;
		height: 0%;
		position: absolute;
		top: 0;
		left: 0;
		background: $red;
		transition: all ease .3s;
	}
	* {
		transition: all ease .3s;
	}
	.option-name, .option-text {
		position: relative;
		z-index: 100;
	}
	.option-name {
		display: block;
		width: 100%;
		font-family: $font_secondary;
		font-size: 18px;
		font-weight: 400;
		line-height: 1.3;
		text-transform: uppercase;
		letter-spacing: 1px;
		margin: 0 0;
		position: relative;
		span.arrow {
			display: block;
			width: 18px;
			height: 18px;
			position: absolute;
			top: 0;
			right: 5px;
			transition: all ease .3s;
			&:before, &:after {
				content:"";
				display: block;
				width: 100%;
				height: 1px;
				background: $primary;
				position: absolute;
				top: 10px;
				left: 0;
				transition: all ease .3s;
			}
			&:before {
				transform: translateX(-7px) rotate(45deg);
			}
			&:after {
				transform: translateX(5px) rotate(-45deg);
			}
		}
	}
	.option-text {
		font-size: 13px;
		padding-top: 10px;
		display: none;
		@include animateThis(1s);
	}
	&.active {
		border-top-color: $red;
		&:before {
			height: 100%;
		}
		.option-name, .option-text {
			color: #FFF;
			span.arrow {
				transform: rotate(180deg);
				&:before, &:after {
					background: #FFF;
				}
			}
		}
		.option-text {
			display: block;
			-webkit-animation-name: fadeIn;
  			animation-name: fadeIn;
		}
	}
}

