.wave-effect {
	position: relative;
	overflow: hidden;
	.wave {
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 120%;
		z-index: 50;
		opacity: 0.75;
		transition: all ease 0.9s;
		transform: translateY(200%);
		&:after {
			content:"";
			display: block;
			width: 100%;
			height: 100%;
			background: #000;
			position: absolute;
			top: 5vw;
			left: 0;
		}
		.waveSvg {
			width: 150%;
			height: 5vw;
			position: absolute;
			top: 0;
			left: 0;
		}
		.circle {
			display: block;
			position: relative;
			width: 100%;
			img {
				width: 100%;
				height: auto;
			}
			&:before, &:after {
				content:"";
				display: block;
				width: 30vw;
				height: 20vw;
				background: #FFF;
				border-radius: 100%;
				position: absolute;
			}
			&:before {
				bottom: 0;
				left: 0;
			}
			&:after {
				bottom: 0;
				left: 0;
			}
		}
	}
	.boxTitle {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 60;
		color: #FFF;
		text-align: center;
		transition: all ease .3s;
		* {
			transition: all ease .8s;
		}
		span {
			display: block;
			width: 100%;
		}
		span.twrap {
			padding: 20px;
			position: absolute;
			top: 55%;
			left: 0;
			opacity: 0;
			transform: translateY(-55%);
			font-family: $font_secondary;
		}
		span.t1 {
			font-size: 18px;
			font-weight: 300;
			line-height: 1.2;
			letter-spacing: 1px;
			text-transform: uppercase;
			margin-bottom: 5px;
		}
		span.t2 {
			font-size: 15px;
		}
	}
	&:hover {
		.wave {
			margin-top: -5vw;
			transform: translateY(0);
		}
		.boxTitle {
			span.twrap {
				opacity: 1;
				top: 50%;
				transform: translateY(-50%);
				transition-delay: .3s;
			}
		}
	}
}

.waveAnimation > use {
  animation: wave 0.5s cubic-bezier(.55,.5,.45,.5)     infinite;
  animation-delay: -2s;
}

@keyframes wave {
  0% {
   transform: translate3d(-90px,0,0);
  }
  100% { 
    transform: translate3d(85px,0,0);
  }
}

